.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  display: flex;
}

.productImage {
  width: 200px;
}

.Products {
  flex: 0 0 75vw;
}

.Products:after { 
  content: " "; 
  display: block;
  clear: both;
} 


.ProductsData {
  width: 25vw;
  float: left;
}

.Cart {
  flex: 1;
}

#order {
  font-size: 1.2em;
  background-color: #3bed59;
  margin: 5%;
  width: 100px;
}

.info {
  margin: 1.5%;
}

.message {
  width: 100vw;
  background-color: #1ee67b;
  opacity: 0.5;
}